@use "../utils" as *;

#main .banner {
  &__left {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  &__left-content {
    position: absolute;
    bottom: 15%;
    left: inherit;

    h2 {
      font-family: var(--font-barlow-regular);
      font-size: em(26);
      letter-spacing: em(2);
      color: #9b9b9b !important;
    }

    h1 {
      font-family: var(--font-bellefair-regular);
      font-size: em(138);
      line-height: 1.15;
    }

    p {
      font-family: var(--font-barlow-regular);
      font-size: em(18);
      letter-spacing: em(0.75);
      line-height: 1.5;
      max-width: em(355);
      color: #9b9b9b !important;
    }
  }

  &__right {
    padding-right: em(55);
  }

  &__right-content {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  &__right-button {
    position: absolute;
    bottom: 15%;
    right: 0;
    width: em(240);
    height: em(240);
    border-radius: 50%;
    background: whitesmoke;

    a {
      font-family: var(--font-bellefair-regular);
      font-size: em(30);
      color: black !important;
    }
  }
}
