@use "../utils" as *;

#main .hdr {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: em(40);
  z-index: 1000;

  @include brkpnt-down(md) {
    padding-top: em(0);
  }

  @include brkpnt-down(sm) {
    padding-top: em(20);
  }

  &__brand {
    position: relative;
    padding-left: em(55);

    a {
      cursor: grab;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: calc(100% + 65%);
        width: em(473);
        height: em(0.75);
        pointer-events: none;
        background-color: rgba(255, 255, 255, 0.25);
        z-index: 5;

        @include brkpnt-down(md) {
          display: none;
        }
      }
    }
  }

  &__navbar {
    position: relative;

    @include brkpnt-down(sm) {
      display: none;
    }
  }

  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: em(30);
    padding-right: em(165);
    padding-bottom: em(30);
    padding-left: em(123);
    backdrop-filter: blur(em(12));
    margin: 0;
    overflow: hidden;
    z-index: 1;

    @include brkpnt-down(md) {
      padding-left: em(40);
      padding-right: em(40);
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      filter: opacity(0.55) brightness(1.55);
      background: linear-gradient(45deg, #272525, #252755);
      opacity: 0.55;
      z-index: -1;
    }
  }

  &__items {
    list-style: none;

    &:not(:last-child) {
      margin-right: em(45);
    }
  }

  &__links {
    cursor: none;
    position: relative;
    font-family: var(--font-barlow-regular);
    font-size: em(16);
    letter-spacing: em(2.7);
    text-transform: uppercase;
    color: whitesmoke;

    @include brkpnt-down(md) {
      letter-spacing: em(0);
    }

    span {
      font-weight: 600;
      margin-right: em(7);

      @include brkpnt-down(md) {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      bottom: calc(0% - 150%);
      left: 0;
      width: 100%;
      height: em(2.75);
      display: inline-block;
      transform: scaleX(0);
      transform-origin: right;
      background: whitesmoke;
      transition: all 300ms;
      transition-property: transform;
      transition-timing-function: cubic-bezier(0.225, 0.795, 0.55, 1.275);
    }

    &:hover::after {
      transform: scaleX(100%);
      transform-origin: left;
    }
  }

  &__links[aria-current="page"] {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: calc(0% - 150%);
      left: 0;
      width: 100%;
      height: em(2.75);
      transform: scaleX(100%);
      background: whitesmoke;
    }
  }
}
